import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cart: []
  },
  mutations: {
    addcar(state,payload) {
      let oldobj = state.cart.find(item => item.iid === payload.iid)
      if (oldobj) {
        oldobj.count += 1
      }
      else {
        payload.count = 1
        payload.check = true
        state.cart.push(payload)
      }
    }
  },
  getters,
  actions: {
  },
  modules: {
  }
})
