<template>
  <div @click="routerClick" class="tabbaritem">
    <div v-show="!isActive">
      <slot name="icon"></slot>
    </div>
    <div v-show="isActive">
      <slot name="icon-active"></slot>
    </div>
    <div :class="{active:isActive}"><slot name="text"></slot></div>
  </div>
</template>

<script>
  export default {
    name: "TabBarItem",
    props: {
      link: {
        type: String,
      }
    },
    computed: {
      isActive() {
        return this.$route.path.indexOf(this.link) !== -1
      }
    },
    methods: {
      routerClick() {
        this.$router.replace(this.link)
      }
    }
  }
</script>

<style scoped>
  .tabbaritem{
    flex: 1;
    text-align: center;
  }
  .tabbaritem img{
    width: 24px;
    height: 24px;
    text-align: center;
    margin-top: 5px;
    vertical-align: middle;
  }
  .tabbaritem div{
    font-size: 14px;
  }
  .active{
    color:red;
  }
</style>